import { FC, memo, useEffect, useState } from "react";

import { setSubscription } from "@src/services";
import axios from "@src/axios";

import Routes from "@src/features/routes";
import { AuthProvider } from "@src/features/auth";

import classes from "./style.module.css";

const AppRaw: FC = () => {
  const [error, setError] = useState<{
    title: string;
    description: string;
  } | null>(null);
  const [sub, setSub] = useState<PushSubscription | null>(null);
  const [isReady, setIsReady] = useState(false);

  useEffect(() => {
    if (sub) {
      setSubscription(sub);
      setIsReady(true);
    }
  }, [sub]);

  useEffect(() => {
    if ("serviceWorker" in navigator) {
      navigator.serviceWorker.ready.then(function (registration) {
        if (!registration.pushManager) {
          setError({
            title: "!",
            description:
              "Push-   .   Google Chrome  Firefox.",
          });
          return;
        }

        //       
        if (Notification.permission === "denied") {
          setError({
            title: "!",
            description:
              "   .     .",
          });
          return;
        }

        registration.pushManager.getSubscription().then((subscription) => {
          setError(null);
          if (!subscription) {
            axios
              .get<string>("/mobile-api/v1/auth/keys")
              .then(({ data: key }) => {
                registration.pushManager
                  .subscribe({
                    applicationServerKey: key,
                    userVisibleOnly: true,
                  })
                  .then((newSubscription) => {
                    setSub(newSubscription);
                  })
                  .catch((e) => {
                    if (Notification.permission === "denied") {
                      setError({
                        title: "!",
                        description:
                          "   .     .",
                      });
                    } else {
                      console.error(
                        "An error ocurred during the subscription process.",
                        e
                      );
                    }
                  });
              })
              .catch((e) => {
                setError({
                  title: " !",
                  description:
                    "       ",
                });
              });
          } else {
            setSub(subscription);
          }
        });
      });
    }
  }, []);

  if (isReady) {
    return (
      <AuthProvider>
        <Routes />
      </AuthProvider>
    );
  }

  if (error) {
    return (
      <div className={classes.error}>
        <div className={classes.errorTitle}>{error.title}</div>
        <div className={classes.errorDesc}>{error.description}</div>
      </div>
    );
  }

  return null;
};

export const App = memo(AppRaw);
export default App;
